import profileMarkup from '../markup/online-profile.js';
const profile = document.querySelector('#list-profile-list');

export default {
  start() {
    profileMarkup.setProfile();
    profile.addEventListener('click', profileMarkup.setProfile);
  },

  stop() {
    profileMarkup.clearProfile();
  }
};



